.zantimes-teams {
  margin-top: 50px;
}
.zantimes-teams__column {
  margin-bottom: 1.5rem;
  cursor: pointer;
}
.zantimes-teams__image {
  width: 100%;
  height: 325px;
  object-fit: cover;
  object-position: top;
}
.zantimes-teams__name {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  font-weight: 700;
}
.zantimes-teams__position {
  font-weight: 700;
  margin-bottom: 0;
}
.zantimes-teams .btn-outline {
  position: relative;
  left: 50%;
  display: inline-block;
  margin-top: 3rem;
  transform: translateX(-50%);
  text-transform: initial;
}
.zantimes-teams__modal {
  top: 0;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1060;
  display: none;
  backdrop-filter: blur(30px);
}
.zantimes-teams__modal .modal-dialog {
  height: 100%;
  display: flex;
  align-items: center;
  max-width: 100%;
}
.zantimes-teams__modal .modal-header {
  padding: 0;
  border: 0;
}
.zantimes-teams__modal .modal-body {
  padding-top: 30px;
  padding-bottom: 30px;
  margin-left: 0 !important;
  margin-right: 0 !important;
}
.zantimes-teams__modal .modal-content {
  background-color: #FFFFFF;
}
.zantimes-teams__modal .modal-content h2 {
  color: #1D0056;
}
.zantimes-teams__modal .modal-content h3 {
  font-size: 20px;
}
.zantimes-teams__modal .modal-content img {
  height: 350px;
  min-height: inherit;
  padding-right: 30px;
}
.zantimes-teams__modal .close {
  margin-left: auto;
  border: none;
  background: transparent;
  font-size: 2em;
}
@media screen and (min-width: 992px) {
  .zantimes-teams__modal .modal-content {
    max-width: 890px;
  }
}
@media screen and (min-width: 768px) {
  .zantimes-teams__modal .modal-header {
    position: absolute;
    top: 0;
    right: 15px;
    z-index: 999;
  }
}
@media screen and (max-width: 991px) {
  .zantimes-teams__modal .modal-content {
    padding: 10px 0px;
    transform: none;
  }
  .zantimes-teams__modal .modal-content img {
    height: 300px;
    padding-right: 0;
    margin-bottom: 1rem;
  }
  .zantimes-teams__modal .modal-body {
    width: 100%;
  }
  .zantimes-teams__modal .close {
    margin-top: 0;
    margin-right: 0;
  }
}
@media screen and (max-width: 767px) {
  .zantimes-teams__modal .modal-content img {
    max-height: 300px;
  }
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}
.modal-backdrop.show {
  z-index: -1;
}

.rtl .zantimes-teams .btn-outline {
  left: 0;
  transform: none;
}

@media screen and (max-width: 991px) {
  body.modal-open {
    position: relative;
    overflow-y: hidden;
    padding-right: 0px;
    height: 100vh;
  }
}